import React, { HTMLAttributes, useState } from 'react';
import { useSpring, animated } from 'react-spring';
import ImpressionArea from 'components/Utils/ImpressionArea';

interface Props extends HTMLAttributes<HTMLDivElement> {
  delay?: number;
}
const FadeUp = ({ children, delay, ...props }: Props) => {
  const [isVisible, setVisible] = useState(false);
  const animation = useSpring({
    opacity: isVisible ? 1 : 0,
    visibility: isVisible ? 'visible' : 'hidden',
    transform: isVisible ? 'translateY(0)' : 'translateY(10%)',
    delay: delay ?? Math.random() * 600,
  });

  return (
    <ImpressionArea onImpressionStart={() => setVisible(true)} {...props}>
      <animated.div style={animation}>{children}</animated.div>
    </ImpressionArea>
  );
};

export default FadeUp;
